


export default function TutorialModal({ open, fecharModal }) {
      if (!open) return null;
    return(
        <tutorialCard>
           <h1>Manual de pareamento</h1> <span className="closeTag" onClick={fecharModal}>X</span>
             <div>
              <h2>1. Pegue o ID do sensor</h2>
              <p>Ao criar o sensor no site, ele ganhou um cdigo nico (ID), mostrado no card lateral. Guarde-o   a identidade do sensor.</p>
              <h2>2. Programe o envio de dados</h2>
              <p>No software do sensor, configure o envio para o endereo de recepo do site (use o boto abaixo para copiar).Sempre envie junto o ID e as medies (temperatura, umidade ou CO).</p>
              <h2>3. Confirmao automtica</h2>
              <p>O site verifica o ID e os dados. Se estiver tudo certo, salva e associa ao seu sensor.</p>
              <h2>4. Acompanhe e ajuste</h2>
              <p>Veja as medies no site, em teampo real ou em grficos, e altere configuraes quando quiser.</p>
             </div>

           <button>Copiar endereo de recepo</button>
  
        </tutorialCard>
    )
}